/* تصميم عام للصفحة */
body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

/* عنوان الصفحة */
h1 {
  text-align: center;
  color: #333;
  margin-top: 20px;
}

/* عداد السلة */
.cart-counter {
  text-align: center;
  margin-bottom: 20px;
  font-weight: bold;
  color: #555;
}

/* حاوية البطاقات */
.products-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 0 20px 40px 20px;
}

/* تصميم البطاقة */
.product-card {
  background-color: #fff;
  border-radius: 10px;
  padding: 15px;
  width: 250px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  transition: transform 0.2s;
}

.product-card:hover {
  transform: translateY(-5px);
}

.product-card img {
  width: 100%;
  border-radius: 5px;
}

.product-card h3 {
  margin-top: 10px;
  color: #333;
}

.product-card p {
  font-size: 14px;
  color: #555;
}

.product-card .price {
  font-weight: bold;
  margin: 10px 0;
}

.product-card button {
  padding: 8px 12px;
  background-color: #ff7f50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
  transition: background-color 0.2s;
}

.product-card button:hover {
  background-color: #e0673f;
}
html {
  scroll-behavior: smooth;
}
.product-card {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  padding: 15px;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 12px rgba(0,0,0,0.2);
}

.product-card img {
  width: 100%;
  border-radius: 10px;
  max-height: 200px;
  object-fit: cover;
}
.cart-btn span {
  background-color: red;
  color: white;
  border-radius: 50%;
  padding: 2px 6px;
  font-size: 12px;
  margin-left: 5px;
}
.products-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  padding: 20px;
}
