.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ff7f50;
  padding: 10px 20px;
  color: white;
}

.navbar .logo {
  font-size: 20px;
  font-weight: bold;
}

.nav-links {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.nav-links li a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

.nav-links li a:hover {
  text-decoration: underline;
}

.cart-btn {
  background: none;
  border: none;
  color: white;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
}

.cart-btn:hover {
  opacity: 0.8;
}
